import React, {useState, useEffect} from "react";
import ARTEx from "../ARTEx 3D";
import InfectionSim from '../InfectionSim.js';
import { div } from "three/examples/jsm/nodes/Nodes.js";

function App() {
  const [isVisible, setIsVisible] = useState(false);
  useEffect(() => {
    const handleResize = () => {
      // Update isVisible based on screen width
      setIsVisible(window.innerWidth > 675); // Set isVisible to true if screen width is greater than 768px
    };

    // Add event listener to handle window resize
    window.addEventListener('resize', handleResize);

    // Initial call to handleResize
    handleResize();

    // Cleanup function to remove event listener
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const [isInfection, setIsInfection] = useState(false);

  const showPITC = () => {
    setIsInfection(!isInfection);
  };

  return (
    <div>
      <h1 >Projects</h1>
      
      <div className="projectDiv">
      <ul className="projectULL">
          <li><h2>All Projects</h2>
          <div className="blur-overflow-container">
          <div className="projectList">
            <h3>University</h3>
            <li className="listTags"><h3>ARTEx</h3><h6>All-terrain Robotic Transportation and Exploration System </h6></li>
            <li className="listTags"><h3>Plague In The City</h3><h6>AI Disease Spreading Simulation </h6></li>
            <li className="listTags"><h3>Bank Code</h3><h6>A java bank to allow users the typical functionality </h6></li>
            <h3>Personal</h3>
            <li className="listTags"><h3>Store</h3><h6>Online Store to Sell Items </h6></li>
            <li className="listTags"><h3>Title</h3><h6>Description </h6></li>
            <li className="listTags"><h3>Title</h3><h6>Description </h6></li>
            <h3>Commisioned</h3>
            <li className="listTags"><h3>Title</h3><h6>Description</h6></li>
            <li className="listTags"><h3>Title</h3><h6>Description </h6></li>
            <li className="listTags"><h3>Title</h3><h6>Last </h6></li>
            </div>
            <div className="blur-container-bottom"></div>
            <div className="blur-container-top"></div>
          </div></li>        
        </ul>
        {isVisible && (
        <ul className="projectUL">
        <h2 className="titles">Top Picks</h2> <li>
          <h3>ARTEx</h3><h6>All-terrain Robotic Transportation and Exploration System (University Project)</h6>
          <div className="display"><ARTEx/></div></li>
            <li>
              <h3>Store</h3><h6>Online Store to Sell (Personal Project)</h6>
              <img className="site-video" src={require("../../images/clothingSite.gif")} alt="Clothing Site" />
        </li>
        <li><h3>Plague In The City</h3><h6>Artificial Intelligence Disease Spreading Simulation (University Project)</h6>
        <button className="pitc-button" onClick={showPITC}><img className="pitc-img" src={require("../../images/Capture.PNG")}/></button>
        {isInfection &&( 
          <div className="showcase" onClick={showPITC}>
            <InfectionSim/>
            <button className="pitc-button" onClick={showPITC}>
              <button onClick={showPITC}>
                <img className="close-icon" src={require("../../images/Icons/close.png")}/>
              </button>
            </button>
          </div> )}
        </li></ul>
        )}
      </div>
    </div>
  );
}

export default App;
