import React, {useState, useEffect} from "react";

function Pages({ page1Height }) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isBlackout, setBlackout] = useState(false);
  const [isMenu, setIsMenu] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMenu(window.innerWidth < 440);
    };

    const handleScroll = () => {
      if (window.scrollY > page1Height - 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener('resize', handleResize);
    window.addEventListener('scroll', handleScroll);

    handleResize(); // Call initially
    return () => {
      window.removeEventListener('resize', handleResize);
      window.removeEventListener('scroll', handleScroll);
    };
  }, [page1Height]);


  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
    setBlackout(!isBlackout);
  };

  return (

    <>
      {!isMenu && ( 
        <div className={scrolled ? "links scrolled" : "links"}>
            <button className="button" onClick="">Home</button>
            <button className="button" onClick="">Projects</button>
            <button className="button" onClick="">Contact</button>
            <button className="button" onClick="">Information</button>
        </div>
      )}

      {isMenu && (<>
        <button className="menuButton" onClick={toggleMenu}>MENU</button>
        
      </>)}
    </>
  );
}

export default Pages;
